import includes from 'lodash/includes'
import userTagTypes from '../../constants/user_tag_types'

const UserTags = {
  props: [
    'tags'
  ],
  computed: {
    isMFCPremiumMember () {
      return includes(this.tags, userTagTypes.MFC_PREMIUM_MEMBER)
    },
    isMFCModel () {
      return includes(this.tags, userTagTypes.MFC_MODEL)
    }
  }
}

export default UserTags



